.movies-main {
    flex-grow: 1;

    display: flex;
    flex-direction: column;
    align-items: center;
    direction: ltr;

    .message {
        margin-top: 25px;
        text-align: center;
        line-height: 1.25;
        border-radius: 7px;

        p {
            font-size: 14px;
            max-width: 40ch;
            padding: 8px 10px;
            border: 3px solid $brand-opc;
            color: $brand;
            border-radius: 7px;
        }
    }

    .movie-list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 10px;

        margin-bottom: 25px;

        button.scroll {
            margin-top: 25px;
            align-self: center;
            color: $white;
            background: $txt;
            opacity: 0.5;
            font-size: 16px;
            padding: 7px 25px;
        }
    }
}

@media (min-width: 641px) {
    .movies-main {
        height: calc(100vh - 135px);

        display: flex;
        flex-direction: column;
        align-items: center;

        position: relative;
        overflow-y: auto;

        .message {
            margin-top: 50px;
            // line-height: 1.5;

            p {
                font-size: 27px;
                // max-width: 40ch;
                padding: 15px 30px;
                border: 5px solid $brand-opc;
            }
        }

        .movie-list {
            width: 1000px;

            display: flex;
            flex-direction: row-reverse;
            flex-wrap: wrap;
            gap: 50px ;

            margin-bottom: 50px;

            button.scroll {
                display: none;
            }
        }
    }
}
