.movie-preview {
    display: flex;
    flex-direction: row-reverse;
    // align-self: center;
    // align-items: center;
    gap: 10px;
    height: 70px;

    width: 350px;
    box-shadow: $shadow;

    .img-container {
        width: 30%;
        overflow: hidden;
        // height: 75px;

        img {
            // height: 100%;
            width: 100%;
            aspect-ratio: 16/9;
            object-fit: cover;
        }
    }

    .mini-details {
        width: 70%;
        display: flex;
        flex-direction: column;
        gap: 5px;

        margin: 5px;

        // white-space: wrap;
        // overflow: hidden;
        // text-overflow: ellipsis;

        h3 {
            text-align: right;
            color: $brand;

            font-size: 14px;
        }

        p {
            text-align: right;
            color: $txt;

            font-size: 12px;

            direction: rtl;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            white-space: wrap;
            text-overflow: ellipsis;
        }
    }
}

@media (min-width: 461px) {
    .movie-preview {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 15px;
        align-self: flex-start;
        height: auto;
        width: 300px;
        margin-bottom: 75px;
        box-shadow: $shadow;
        cursor: pointer;

        &:hover {
            margin-bottom: 0;

            .mini-details {
                h3 {
                    margin-bottom: 0px;
                }

                p {
                    display: block;
                    text-align: center;
                    color: $txt;

                    font-size: 14px;
                    padding: 0 15px 15px;
                }
            }
        }

        .img-container {
            height: 150px;
            width: 100%;

            img {
                width: 100%;
                object-fit: cover;
            }
        }

        .mini-details {
            width: 100%;
            margin: 0;

            h3 {
                text-align: center;
                color: $brand;

                font-size: 18px;
                margin-bottom: 15px;
            }

            .short-des {
                display: none;
            }
        }
    }
}
