.movie-details {
    flex-grow: 1;
    direction: rtl;
    background-image: linear-gradient(to bottom, transparent, $brand-opc);
    background-size: 100% 35%;
    background-position: bottom;
    background-repeat: no-repeat;

    padding: 25px;
    // overflow-y: auto;

    display: flex;
    flex-direction: column-reverse;
    gap: 25px;

    .movie-container {
        box-shadow: $shadow-movie;

        .img-container {
            overflow: hidden;
            width: 100%;
            aspect-ratio: 16/9;

            img {
                height: 100%;
                object-fit: cover;
            }
        }
    }

    .info {
        flex-grow: 1;
        display: flex;
        flex-direction: column;

        h2.title {
            color: $brand;
            font-size: 22px;

            margin-bottom: 4px;
        }

        .catagories {
            display: flex;
            flex-wrap: wrap;
            gap: 5px;

            margin: 15px 0;

            & > * {
                color: $white;
                background-color: $txt;
                border-radius: 100px;

                font-size: 14px;
                padding: 5px 10px;
            }
        }

        .des {
            display: flex;
            flex-direction: column;
            gap: 7px;

            p.full-des {
                font-size: 16px;
                line-height: 1.35;
            }

            .credits {
                margin-bottom: 5px;
                p.credit {
                    font-size: 14px;
                }
            }
        }

        .extra-details {
            font-size: 14px;
        }

        .movie-activities {
            margin-top: 25px;
            width: fit-content;

            background-color: $brand-opc;
            border-radius: 7px;
            padding: 25px;

            .title {
                display: flex;
                align-items: center;
                gap: 15px;
                margin-bottom: 5px;
                color: $brand;

                svg {
                    height: 35px;
                    transform: translateY(-2px);
                }
            }

            .acts-list {
                padding-inline-start: 25px;
                margin-bottom: 0;

                & > *:not(last-child) {
                    margin-bottom: 7px;
                }

                .act-preview {
                    color: $brand;

                    span {
                        color: $txt;
                    }
                }
            }
        }

        .btns-container {
            display: flex;
            justify-content: space-between;

            margin-top: 50px;

            button {
                font-size: 16px;
                padding: 10px 18px;
            }

            .watch {
                color: $white;
                background-color: $brand;
                border: none;
            }

            .back {
                color: $brand;
                background: $bg;
            }
        }
    }
}

@media (min-width: 641px) {
    .movie-details {
        direction: rtl;
        height: calc(100vh - 135px);
        width: 100vw;

        display: flex;
        flex-direction: row;
        gap: 150px;

        background-color: $bg;
        padding: 100px 200px;

        .info {
            flex-basis: 50%;

            height: 100%;

            display: flex;
            flex-direction: column;

            h2.title {
                color: $brand;

                font-size: 25px;
                margin-bottom: 7px;
            }

            span.length {
                display: block;
                font-weight: 300;
            }

            .catagories {
                gap: 10px;
                margin: 35px 0;

                & > * {
                    color: $white;
                    background-color: $txt;
                    border-radius: 100px;

                    font-size: 16px;
                    padding: 5px 10px;
                }
            }

            .des {
                display: flex;
                flex-direction: column;

                p.full-des {
                    max-width: 75ch;
                    font-size: 17px;
                    line-height: 1.35;
                }
            }

            .movie-activities {
                .title {
                    // font-size: 20px;
                }

                .acts-list {
                    // font-size: 16px;
                }
            }

            .btns-container {
                justify-content: flex-start;
                gap: 25px;

                margin-top: 50px;

                button {
                    font-size: 16px;
                    padding: 10px 15px;
                }

                .watch {
                    border: none;
                    background-color: $brand;
                    color: $white;
                }

                .back {
                    background: transparent;
                    border: 1px solid $brand;

                    &:hover {
                        background-color: $white;
                    }
                }
            }
        }

        .movie-container {
            flex-basis: 50%;
            position: relative;
            overflow: hidden;
            align-self: flex-start;
            height: fit-content;

            .img-container {
                img {
                    width: 100%;
                    aspect-ratio: 16/9;
                    object-fit: cover;
                }
            }
        }
    }
}
