header {
    display: flex;
    justify-content: center;
    align-items: center;

    background-color: $header;
    box-shadow: $shadow-bottom;
    padding: 5px;

    .img-container {
        overflow: hidden;
        height: 50px;
        cursor: pointer;

        img {
            transform: translateY(4px);
            height: 50px;
            object-fit: contain;
        }
    }
}

@media (min-width: 641px) {
    header {
        height: 100px;

        .img-container {
            height: 75px;

            img {
                transform: translateY(6px);
                height: 75px;
            }
        }
    }
}
